import { defineComponent, reactive, ref } from 'vue';
import { rules } from '../config/account.config';
import localStorage from '../../../utils/cache';
import { useStore } from 'vuex';
export default defineComponent({
    setup() {
        const store = useStore();
        const account = reactive({
            name: 'coderdong',
            password: '123456' //localStorage.getCache('password') ?? ''
        });
        const formRef = ref();
        const loginAction = (keepPassword) => {
            formRef.value?.validate((valid) => {
                if (valid === true) {
                    if (keepPassword === true) {
                        localStorage.setCache('name', account.name);
                        localStorage.setCache('password', account.password);
                    }
                    console.log('登录成功的逻辑');
                    store.dispatch('login/accountLoginAction', {
                        ...account,
                        name: 'coderwhy'
                    });
                }
                else {
                    console.log('登录失败的逻辑');
                }
            });
        };
        return {
            account,
            rules,
            loginAction,
            formRef
        };
    }
});
